import apiUrl from '@/api/apiUrl'
import httpService from '@/api/httpService'
import { util } from '@/utils/util'
import { Toast } from 'vant';

export default {
    created() {
    //     this.number =  this.$store.state.user.mySelfListStatus ;
    //    if(this.number >= 4){
    //     //    表示竞拍

    //    }else{
    //     //    表示拍卖

    //    }
    },
    data() {
        return{
            goodsstatus:0,
            number:0,
            barList1: ['进行中拍卖','拍卖成功','拍卖失败'],
            barList2: ['进行中竞拍','竞拍成功','竞拍失败'],
            status: this.$store.state.user.status,
            active: this.$store.state.user.active,
            finished: false,
            refreshLoading: false,
            loading: false,
            commodityList: [],

            pageSize: 10,
            currentPage: 1,
            loadingArr:[false,false,false]
        }
    },
    computed: {
        title() {
            if(this.number <= 3) {
                return '参与的拍卖'
            }else {
                return '参与的竞拍'
            }
        }
    },
    methods: {
        onLoad() {

            this.updateList();
        },
        onRefresh(index) {

            this.currentPage === 1
            // this.loading =true;
            this.updateList()
            
        },
        backHandler() {
            this.$router.go(-1)
        },
        goDetailHandler(index) {
            this.goodsstatus = this.commodityList[index].status
            this.$router.push({
                path: '/detail',
                name: 'Detail',
                query: {
                    goodsId: this.commodityList[index].goodsId,
                    shwoBtn:true,
                    goodsstatus:this.goodsstatus
                }
            })
        },
        updateList() {
            let opts 
            if(this.status === 0) {
                if(this.active === 0) {
                    opts = {  
                        apiObj: apiUrl.findGoodsBySellerIdAndStatus,
                        query: {
                            sellerId: this.$store.state.user.id,
                            size: this.pageSize,
                            page: this.currentPage - 1,
                            status: 1
                        }
                    }
                }else if(this.active === 1) {
                    opts = {  
                        apiObj: apiUrl.findGoodsBySellerIdAndStatus,
                        query: {
                            sellerId: this.$store.state.user.id,
                            size: this.pageSize,
                            page: this.currentPage - 1,
                            status: 2
                        }
                    }
                }else if(this.active === 2) {
                    opts = {  
                        apiObj: apiUrl.findGoodsBySellerIdAndStatus,
                        query: {
                            sellerId: this.$store.state.user.id,
                            size: this.pageSize,
                            page: this.currentPage - 1,
                            status: 3
                        }
                    }
                } 
            }else if(this.status === 1) {
                if(this.active === 0) {
                    opts = {  
                        apiObj: apiUrl.findGoodsByBuyerIdAndStatus,
                        query: {
                            buyerId: this.$store.state.user.id,
                            size: this.pageSize,
                            page: this.currentPage - 1,
                            status: 1
                        }
                    }
                }else if(this.active === 1) {
                    opts = {  
                        apiObj: apiUrl.findGoodsByBuyerIdAndStatus,
                        query: {
                            buyerId: this.$store.state.user.id,
                            size: this.pageSize,
                            page: this.currentPage - 1,
                            status: 2
                        }
                    }
                }else if(this.active === 2) {
                    opts = {  
                        apiObj: apiUrl.findGoodsByBuyerIdAndStatus,
                        query: {
                            buyerId: this.$store.state.user.id,
                            size: this.pageSize,
                            page: this.currentPage - 1,
                            status: 3
                        }
                    }
                } 
            }

            httpService.accessAPI(opts)
                .then(res => {
                    if(res.code !== '0'){
                        Toast(res.msg);
                        return ;
                    }
                    
                    console.log(this.loading);
                    this.commodityList.splice(0);
                    this.commodityList = res.data;
                    this.commodityList.forEach(item => {
                        item.endTime = util.dateToStr(new Date(item.endTime), 5)
                    })
                    this.commodityList.forEach(item => {
                        item.imgs = item.imgs.split(";");
                        console.log(item.imgs[0]);
                    })
                    this.loading = false;
                    this.refreshLoading = false;

                    // this.loadingArr[index-1] =false;/
                    if(this.commodityList.length <= this.currentPage * this.pageSize) {
                        this.finished = true;
                    }else {
                        this.currentPage ++
                    }
                }, rej => {
                    this.commodityList = []
                    this.loading = false;
                    this.finished = true;
                })
        }
    },
    watch: {
        active() {
            this.$store.commit('user/setStatusA', {status: this.status, active: this.active})
            this.updateList()
        }
    }
}



